<template>
    <p-card style="width: 800px; margin: auto">
        <template #title>
            <strong>Medication Inventory</strong>
        </template>

        <template #content>
            <p-data-table
                :value="inventoryList"
                responsiveLayout="scroll"
                showGridlines
                class="p-datatable-sm p-mb-1"
            >
                <p-column header="Name" field="name" />
                <p-column header="Description" field="description" />
                <p-column header="Category" field="code" :sortable="true" />
                <p-column style="width: 50px">
                    <template #body="{data}">
                        <p-button
                            icon="pi pi-pencil"
                            @click="showEditMedicationInventoryDialog(data)"
                        />
                    </template>
                </p-column>
            </p-data-table>

            <div class="p-d-flex p-jc-between p-mt-2">
                <p-button
                    label="Add"
                    autofocus
                    @click="showAddMedicationInventoryDialog"
                />
            </div>
        </template>
    </p-card>

    <p-dialog
        v-model:visible="addMedicationInventoryDialog"
        :modal="true"
        style="width: 600px"
    >
        <template #header>
            <div class="table-header">
                <strong>Add Medication</strong>
            </div>
        </template>

        <div class="p-fluid">
            <div class="p-field">
                <label>Category</label>
                <p-dropdown v-model="form.code" :options="categoryOpts" />
            </div>
            <div class="p-field">
                <label>Name</label>
                <p-input-text v-model="form.name" />
            </div>
            <div class="p-field">
                <label>Description</label>
                <p-textarea v-model="form.description" />
            </div>
        </div>

        <div class="p-d-flex p-jc-between p-mt-2">
            <p-button
                label="Update"
                autofocus
                @click="edit"
                v-if="formIsEdit"
            />
            <p-button label="Save" autofocus @click="save" v-else />
        </div>
    </p-dialog>
</template>

<script>
import { computed, inject, onMounted, reactive, ref } from "vue";
import { useStore } from "vuex";
export default {
    setup() {
        const store = useStore();

        const addMedicationInventoryDialog = ref(false);
        const inventoryList = computed(
            () => store.state.cycleMonitoring.inventoryList
        );

        const categoryOpts = reactive(["Stimulation", "Luteal", "OPU"]);

        const form = reactive({
            id: null,
            code: null,
            name: null,
            description: null
        });

        const formIsEdit = ref(false);

        const save = () => {
            store
                .dispatch("cycleMonitoring/saveInvetory", { ...form })
                .then(() => (addMedicationInventoryDialog.value = false));
        };

        const edit = (data) => {
            store
                .dispatch("cycleMonitoring/updateInvetory", { ...form })
                .then(() => (addMedicationInventoryDialog.value = false));
        };

        onMounted(() => {
            if (inventoryList.value.length == 0) {
                store.dispatch("cycleMonitoring/getInventoryList");
            }
        });

        return {
            form,
            formIsEdit,
            save,
            edit,
            addMedicationInventoryDialog,
            inventoryList,
            categoryOpts,
            showAddMedicationInventoryDialog: () => {
                formIsEdit.value = false;

                form.id = null;
                form.code = null;
                form.name = null;
                form.description = null;

                addMedicationInventoryDialog.value = true;
            },
            showEditMedicationInventoryDialog: (data) => {
                formIsEdit.value = true;

                form.id = data.id;
                form.code = data.code;
                form.name = data.name;
                form.description = data.description;

                addMedicationInventoryDialog.value = true;
            }
        };
    }
};
</script>

<style scoped>
.table-header {
    display: flex;
    align-items: ;
    justify-content: space-between;
}
</style>
